}

header {
  background-image: url("../img/header.jpg");
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

header .wrap .sidebar .logotype {
  padding-top: 20px;
  padding-bottom: 20px;
  width: 25%;
  line-height: 0;
}

header .wrap .sidebar nav {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
  margin-top: auto;
  font-weight: 700;
  font-size: 0.8em;
  margin-bottom: 20px;
}

.item {
  border-right: 1px solid #000;
  padding: 2px 15px 0;
  line-height: 1.6em;
}

.item:last-child {
  border: none;
}

.item:hover {
  cursor: pointer;
  background-color: rgba(6, 6, 6, 0.3);
}

.log {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: flex-end;
}

.log .item {
  margin: 30px 0;
  border: 1px solid #000;
}

main .wrap .projects {
  margin: 0 5px;
  margin-left: -5px;
  margin-right: -5px;
}

main .wrap .projects::after {
  display: block;
  content: "";
  clear: both;
}

main .wrap .projects .project {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  float: left;
  width: calc(25% - 10px);
  position: relative;
  overflow: hidden;
}

main .wrap .projects .project:hover {
  cursor: pointer;
}

main .wrap .projects .project:hover .discription {
  opacity: 1;
  bottom: 0;
}

main .wrap .projects .project .discription {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 100%;
  left: 0;
  text-align: center;
  color: #fff;
  margin-bottom: 5%;
  padding-top: 10%;
  line-height: 25px;
  opacity: 0;
  transition: all .80s;
}

main .wrap .projects .project .discription .textWrap {
  padding: 42% 2% 0;
}

main .wrap .projects .project .discription .textWrap .text {
  display: inline-block;
  vertical-align: middle;
}

section .wrap .we {
  margin-bottom: 30px;
}

section .wrap .mainMembers {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
}

section .wrap .mainMembers .member {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(29.16667% - 10px);
  margin-left: calc(4.16667% + 5px);
  margin-right: calc(4.16667% + 5px);
}

section .wrap .members {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

section .wrap .members .member {
